import { useState } from 'react'
import { useStore } from '../App'
import DropdownSelect from '../components/dropdown'
import questions from '../data/questions'

const SectionTwo = () => {
  const section = 1
  const sectionQuestions = questions[section]
  const [showQuestion, setShowQuestion] = useState(-1)

  const setSection2Answers = useStore((state) => state.setSection2Answers)

  const handleChange = (questionIndex) => (item) => {
    setSection2Answers(
      {
        item: item.selectedItem.text || item.selectedItem,
        key: sectionQuestions[questionIndex].key,
      },
      questionIndex
    )
    // setSection2Answers(item.selectedItem.text || item.selectedItem, questionIndex)

    if (item.selectedItem.showQuestion) {
      setShowQuestion(item.selectedItem.showQuestion)
    }
  }

  return (
    <section>
      {sectionQuestions.map((question, questionIndex) => {
        if (question.hidden) {
          if (showQuestion === questionIndex) {
            return (
              // hidden question
              <DropdownSelect
                id={questionIndex}
                section={section}
                question={question.question}
                subText="Sagitiis amet, neque, tortor enim sem."
                items={question.options}
                key={`question-${questionIndex}`}
                hidden={question.hidden}
                handleChange={handleChange(questionIndex)}
              />
            )
          } else {
            return null
          }
        } else {
          return (
            // visible question
            <DropdownSelect
              id={questionIndex}
              section={section}
              question={question.question}
              subText="Sagitiis amet, neque, tortor enim sem."
              items={question.options}
              key={`question-${questionIndex}`}
              hidden={question.hidden}
              handleChange={handleChange(questionIndex)}
            />
          )
        }
      })}
    </section>
  )
}

export default SectionTwo
