import Button from '../components/button'

const SummaryButtons = ({ setVisibleSection, visibleSection }) => (
  <>
    <Button
      icon="next"
      variant="outline"
      onClick={() => {
        setVisibleSection(0)
      }}
    >
      Start an application
    </Button>
    <Button
      icon="next"
      onClick={() => {
        setVisibleSection(visibleSection + 1)
      }}
    >
      Get resources to help with your application
    </Button>
  </>
)

export default SummaryButtons
