import cn from 'classnames'
import ChevronLeft from '../chevron-left'
import ChevronRight from '../chevron-right'
import styles from './button.module.css'

export default function Button({
  children,
  icon,
  variant,
  className,
  ...rest
}) {
  return (
    <button
      className={cn(
        styles.button,
        {
          [styles.outline]: variant === 'outline',
          [styles.hasIconPrevious]: icon === 'previous',
          [styles.hasIconNext]: icon === 'next',
        },
        className
      )}
      {...rest}
    >
      {icon === 'previous' && (
        <ChevronLeft
          className={cn(styles.icon, styles.iconPrevious)}
          strokeWidth="3"
        />
      )}
      {children}
      {icon === 'next' && (
        <ChevronRight
          className={cn(styles.icon, styles.iconNext)}
          strokeWidth="3"
        />
      )}
    </button>
  )
}
