import Button from '../components/button'
import { useStore } from '../App'
import mapAnswersToPostString from '../utils/mapAnswersToPostString'

const SectionTwoButtons = ({ setVisibleSection, visibleSection }) => {
  const section1Answers = useStore((state) => state.section1Answers)
  const section2Answers = useStore((state) => state.section2Answers)

  return (
    <>
      <Button
        icon="previous"
        variant="outline"
        onClick={() => {
          setVisibleSection(visibleSection - 1)
        }}
      >
        Back
      </Button>
      <Button
        icon="next"
        onClick={() => {
          console.log(section1Answers)
          console.log(section2Answers)
          console.log(mapAnswersToPostString(section1Answers, section2Answers))
          setVisibleSection(visibleSection + 1)
        }}
      >
        Submit
      </Button>
    </>
  )
}

export default SectionTwoButtons
